export default {
    data() {
        return {
            category: [],
            series: [],
            error: null,
            urlApi: 'http://localhost:1337',
        }
    },
    async beforeCreate() {
        try {
            this.category = await this.$strapi.$categories.find()
            this.series = await this.$strapi['$product-series'].find()
        } catch (error) {
            this.error = error
        }
    },
}
